.btn-clear {
  width: fit-content;
  padding: 5px 10px;
  background-color: rgb(223, 84, 84);
  color: var(--color-secundario);
  border: none;
  border-radius: 7px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

.btn-clear:hover {
  background-color: rgb(107, 34, 34);
}